}
body {
  font-family: 'Palanquin', sans-serif;
}
h1,
h2 {
  font-family: 'Palanquin Dark', sans-serif;
  font-weight: lighter;
}
nav {
  width: 100%;
  padding: 1% 0;
  display: flex;
  justify-content: baseline;
  position: sticky;
  top: 0;
  background-color: white;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.5);
}
@media ( max-width: 800px ) {
  nav {
    justify-content: center;
  }
}
nav a {
  margin: 0 2%;
  padding: 0.3% 0.5% 0.5%;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bolder;
  color: inherit;
  border: 5px solid transparent;
}
nav a:hover {
  border: 5px solid black;
}
nav a:active {
  border: 5px double black;
}
nav .current-page {
  background-color: black;
  color: white;
}
nav .current-page:hover {
  color: white;
  box-shadow: none;
}
header h1 {
  padding-left: 2%;
}
header img {
  max-width: 100%;
}
footer {
  margin: 5% 0 3%;
  text-align: center;
}
footer a {
  color: inherit;
}
footer a:hover {
  color: grey;
}
.posts {
  display: flex;
  flex-direction: column-reverse;
}
.post {
  padding: 0 2% 2%;
  margin-bottom: 3%;
  border-bottom: dashed 0.7px rgba(0, 0, 0, 0.5);
}
.post h2 {
  margin-bottom: 0;
}
.post time {
  color: grey;
}
.post img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}
.post a {
  color: inherit;
}
.post a:hover {
  color: grey;
}
.post p {
  line-height: 1.5;
  font-size: 1.2rem;
}
.post button {
  background-color: black;
  border: 5px black solid;
  color: white;
  padding: 1% 2% 1%;
  font-family: 'Palanquin Dark', sans-serif;
  border: 5px solid black;
}
.post button:hover {
  border: 5px solid black;
}
.post button:active {
  border: 5px double black;
}
.post button:hover {
  background-color: white;
  color: black;
}
.post .nasaContainer {
  max-width: 75%;
  margin: 2% auto;
  padding: 2% 0;
  border: solid black 1px;
}
.post .nasaContainer .nasaTitle {
  text-align: center;
}
.post .nasaContainer .nasaDate {
  text-align: center;
  margin: 1% 0 0;
}
.post .nasaContainer .nasaDesc {
  font-size: 1.1em;
  padding: 2%;
  line-height: 1.3;
}
.post .nasaContainer .nasaLink {
  padding: 2%;
}
.col {
  max-width: 800px;
  margin: -1% auto;
  padding: 1% 0;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5), -2px 2px 3px rgba(0, 0, 0, 0.5);
}
@media ( max-width: 800px ) {
  .col {
    width: 100%;
  }
}
